import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/producaoRural';

const ProducaoRuralIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'referencia',
            text: 'Referência',
          },
          {
            dataField: 'vrPessoaJuridica',
            text: 'Valor Pessoa Jurídica',
          },
          {
            dataField: 'vrPessoaFisica',
            text: 'Valor Pessoa Física',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'ProducaoRuralSearchView',
      }}
      label={props.label ? props.label : 'Producao Rural'}
      placeholder={props.placeholder ? props.placeholder : 'Informe a Produção Rural'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default ProducaoRuralIS;
