import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/pagamentoCooperativa';

const PagamentoCooperativaIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'referencia',
            text: 'Referência',
          },
          {
            dataField: 'valor',
            text: 'Valor',
          },
          {
            dataField: 'aliquota',
            text: 'Alíquota',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'PagamentoCooperativaSearchView',
      }}
      label={props.label ? props.label : 'Pagamento Cooperativa'}
      placeholder={props.placeholder ? props.placeholder : 'Informe o Pagamento'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default PagamentoCooperativaIS;
