import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertType,
  ColumnsProps,
  compare,
  DATE_MONTH_YEAR_PATTERN,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditViewProps,
  getSelectedItem,
  InputGroupInline,
  InputType,
  parseDateToRef,
  REF_PATTERN,
  Tab,
  TabGroup,
  toDate,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, GuiaGps } from '../../../../models/dtos/mfp/guiaGps';
import { TIPO_GPS_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoGpsEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import CodigoGpsIS from '../../../components/mfp/codigoGpsIS/CodigoGpsIS';
import FpasIS from '../../../components/mfp/fpasIS/FpasIS';
import GuiaGpsIS from '../../../components/mfp/guiaGpsIS/GuiaGpsIS';
import PagamentoCooperativaIS from '../../../components/mfp/pagamentoCooperativaIS/PagamentoCooperativaIS';
import ProducaoRuralIS from '../../../components/mfp/producaoRuralIS/ProducaoRuralIS';
import TomadorServicoIS from '../../../components/mfp/tomadorServicoIS/TomadorServicoIS';
import VinculoIS from '../../../components/mfp/vinculoIS/VinculoIS';
import CompensacaoGpsEditView from '../compensacaoGps/CompensacaoGpsEditView';

interface GuiaGpsEditViewProps extends EditViewProps<GuiaGps> {}

const GuiaGpsEditView: FC<GuiaGpsEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const { globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { reset, ...formProps } = useForm<GuiaGps>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });
  const [isCalcularPorTomador, setIsCalcularPorTomador] = useState();
  const labelWidth = 200;

  useEffect(
    () => {
      if (!formProps.getValues().id) {
        reset({
          vrAcidenteTrabalho: 0,
          vrEmpresa: 0,
          vrINSSDescontado: 0,
          vrJuros: 0,
          vrMulta: 0,
          vrRecolhimentoMinimo: 0,
          vrSocioAutonomo: 0,
          vrTerceiros: 0,
        });
      }

      if (globalParameter && globalParameter.empresaMFP) {
        setIsCalcularPorTomador(globalParameter.empresaMFP.calcularGpsTomador);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const colunasCompensacao: ColumnsProps[] = [
    {
      dataField: 'compensacao.referencia',
      text: 'Referência',
    },
    {
      dataField: 'valor',
      text: 'Valor',
    },
    {
      dataField: 'compensacao.observacao',
      text: 'Observação',
    },
    {
      dataField: 'compensacao.tomador.nome',
      text: 'Tomador',
    },
  ];

  const compensacaoFormProps: DetailManagerFormProps = {
    columns: colunasCompensacao,
    detailView: CompensacaoGpsEditView,
    formProps,
    propName: 'compensacoes',
    title: 'Guia - Compensação',
    wikiId: 892,
  };

  useEffect(() => {
    atualizaValores();
  });

  const atualizaValores = () => {
    const { alteradoManualmente, vrEmpresa, vrSocioAutonomo, vrAcidenteTrabalho, gpsAcumulada, vrTerceiros, compensacoes } = formProps.getValues();
    if (alteradoManualmente) {
      // Atualiza valor Dedutivel
      const vd = vrEmpresa + vrSocioAutonomo - vrAcidenteTrabalho;
      formProps.setValue('vrDedutivel', vd > 0 ? vd : 0);

      // Atualiza valor INSS
      let vrOutrasEntidades;
      let vrAcumulado;

      if (gpsAcumulada) {
        if (vrOutrasEntidades) {
          vrOutrasEntidades = gpsAcumulada.vrOutrasEntidades;
        }
        vrAcumulado = gpsAcumulada.vrINSS - vrOutrasEntidades;
      }
      formProps.setValue('vrINSS', formProps.getValues().vrDedutivel + vrAcumulado);

      // Atualiza valor Outras Entidades
      vrAcumulado = gpsAcumulada ? gpsAcumulada.vrOutrasEntidades : 0;
      formProps.setValue('vrOutrasEntidades', vrTerceiros + vrAcumulado);

      if (compensacoes.length > 0) {
        formProps.setValue(
          'vrCompensado',
          compensacoes.filter(it => it.compensar).reduce((sum: number, comp) => sum + +comp.valor, 0)
        );
      }
    }

    atualizaValorTotal();
  };

  const atualizaValorTotal = () => {
    const { alteradoManualmente, vrINSS, vrOutrasEntidades, vrJuros, vrMulta, vrCompensado } = formProps.getValues();

    if (alteradoManualmente) {
      formProps.setValue('vrTotal', vrINSS + vrOutrasEntidades + vrJuros + vrMulta - vrCompensado);
    }
  };

  const validateExtras = () => {
    const erros = [];
    const { gpsAcumulada, tomador } = formProps.getValues();

    if (globalParameter.empresaMFP && globalParameter.empresaMFP.calcularGpsTomador !== isCalcularPorTomador && tomador) {
      erros.push(
        'Não é possível cadastrar uma Guia com tomador. Acesse: Manutenção > Configurações > Empresa > Aba GPS e selecione a opção Calcular GPS por Tomador'
      );
    }

    if (gpsAcumulada && gpsAcumulada.dtVencimento) {
      if (compare(parseDateToRef(toDate(gpsAcumulada.referencia, DATE_MONTH_YEAR_PATTERN)), formProps.getValues().referencia, REF_PATTERN) !== -1) {
        erros.push('A referência da GPS acumulada deve ser menor do que a referência da GPS atual.');
      }
    }
    return erros;
  };

  const beforePersist = (formData: GuiaGps, done: any) => {
    formData.empresaMFP.id = globalParameter.empresaMFP.id;
    formData.alteradoManualmente = true;
    if (formData.pagamentoCooperativa || formData.producaoRural) {
      formData.compensacoes = [];
    }

    alertManager.emit(ACTION_EVENTS.SHOW, {
      message: 'Você deseja realmente salvar esta guia? Os valores calculados passarão a ser desconsiderados.',
      onOkClick: () => done(formData),
      type: AlertType.WARNING_YES_NO,
    });

    return { async: true };
  };

  return (
    <WindowComponent
      title="GPS (Guia)"
      useGlobalParameter={true}
      globalParameter={globalParameter}
      dependencies={[() => hasEmpresa(globalParameter), () => hasEmpresaMFP(globalParameter)]}
      wikiId={892}
    >
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} beforePersist={beforePersist} validateExtras={validateExtras}>
        <InputGroupInline id="empresaMFP.id" formProps={formProps} hidden={true} />
        <TabGroup formProps={formProps}>
          <Tab title="Gerais">
            <Row>
              <Col>
                <InputGroupInline
                  labelWidth={labelWidth}
                  id="referencia"
                  label="Referência"
                  isPrependLabel={true}
                  formProps={{ ...formProps, validation: { required: true } }}
                  placeholder="Informe a Referência"
                  type={InputType.REFERENCE}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  labelWidth={labelWidth}
                  id="dtVencimento"
                  label="Dt. de Vencimento"
                  isPrependLabel={true}
                  formProps={{ ...formProps, validation: { required: true } }}
                  placeholder="Informe a Dt. Vencimento"
                  type={InputType.DATE}
                />
              </Col>
              <Col>
                <InputGroupInline
                  id="dtVencimentoAtraso"
                  label="Dt. de Vencimento (Atraso)"
                  isPrependLabel={true}
                  formProps={formProps}
                  type={InputType.DATE}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  labelWidth={labelWidth}
                  id="tipo"
                  label="Tipo"
                  isPrependLabel={true}
                  placeholder="Selecione o Tipo GPS"
                  formProps={{ ...formProps, validation: { required: true } }}
                  inputDropDownProps={{
                    optionsProvider: TIPO_GPS_ENUM_VALUES,
                  }}
                  type={InputType.DROP_DOWN}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  labelWidth={labelWidth}
                  id="vrEmpresa"
                  label="Empresa"
                  isPrependLabel={true}
                  formProps={formProps}
                  type={InputType.NUMBER}
                />
              </Col>
              <Col>
                <InputGroupInline
                  labelWidth={labelWidth}
                  id="vrINSSDescontado"
                  label="Vr.INSS Descontado"
                  isPrependLabel={true}
                  formProps={formProps}
                  type={InputType.NUMBER}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  labelWidth={labelWidth}
                  id="vrSocioAutonomo"
                  label="Sócio/Autônomo"
                  isPrependLabel={true}
                  formProps={formProps}
                  type={InputType.NUMBER}
                />
              </Col>
              <Col>
                <InputGroupInline
                  labelWidth={labelWidth}
                  id="vrRecolhimentoMinimo"
                  label="Recolhimento Mínimo"
                  isPrependLabel={true}
                  formProps={formProps}
                  type={InputType.NUMBER}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  labelWidth={labelWidth}
                  id="vrAcidenteTrabalho"
                  label="Acidente de trabalho"
                  isPrependLabel={true}
                  formProps={formProps}
                  type={InputType.NUMBER}
                />
              </Col>
              <Col>
                <InputGroupInline
                  labelWidth={labelWidth}
                  id="vrTerceiros"
                  label="Terceiros"
                  isPrependLabel={true}
                  formProps={formProps}
                  type={InputType.NUMBER}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  labelWidth={labelWidth}
                  id="vrJuros"
                  label="Juros"
                  isPrependLabel={true}
                  formProps={formProps}
                  type={InputType.NUMBER}
                />
              </Col>
              <Col>
                <InputGroupInline
                  labelWidth={labelWidth}
                  id="vrMulta"
                  label="Multa"
                  isPrependLabel={true}
                  formProps={formProps}
                  type={InputType.NUMBER}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CodigoGpsIS
                  labelWidth={labelWidth}
                  id="gps"
                  formProps={{ ...formProps, validation: { required: true } }}
                  isPrependLabel={true}
                  label="GPS"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <FpasIS labelWidth={labelWidth} id="fpas" formProps={formProps} isPrependLabel={true} label="FPAS" />
              </Col>
            </Row>
            <Row>
              <Col>
                <GuiaGpsIS labelWidth={labelWidth} id="gpsAcumulada" formProps={formProps} isPrependLabel={true} label="GPS Acumulada" />
              </Col>
            </Row>
            <Row>
              <Col>
                <TomadorServicoIS
                  labelWidth={labelWidth}
                  id="tomador"
                  formProps={formProps}
                  label="Tomador"
                  placeholder="Selecione o Tomador"
                  isPrependLabel={true}
                  inputSearchProps={{ selectProps: { isMulti: false } }}
                  disabled={!isCalcularPorTomador}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <VinculoIS
                  labelWidth={labelWidth}
                  id="vinculo"
                  formProps={formProps}
                  label="Doméstico"
                  isPrependLabel={true}
                  inputSearchProps={{ selectProps: { isMulti: false } }}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <PagamentoCooperativaIS labelWidth={labelWidth} id="pagamentoCooperativa" formProps={formProps} label="Pagamento Cooperativa" />
              </Col>
            </Row>
            <Row>
              <Col>
                <ProducaoRuralIS labelWidth={labelWidth} id="producaoRural" formProps={formProps} label="Produção Rural" />
              </Col>
            </Row>
          </Tab>
          <Tab title="Compensações/Deduções">
            <Col>
              <DetailManagerForm {...props} {...compensacaoFormProps} keyField="rowIndex" />
            </Col>
          </Tab>
        </TabGroup>
        <Row>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              formProps={formProps}
              id="vrTotal"
              isPrependLabel={true}
              label="Total"
              type={InputType.NUMBER}
              disabled={true}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default GuiaGpsEditView;
