import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { CompensacaoGps } from '../../../../models/dtos/mfp/compensacaoGps';
import { GuiaGps } from '../../../../models/dtos/mfp/guiaGps';
import CompensacaoIS from '../../../components/mfp/compensacaoIS/CompensacaoIS';

const CompensacaoGpsEditView: FC<DetailEditViewFormProps<CompensacaoGps>> = props => {
  const formProps = useForm<CompensacaoGps>({ defaultValues: { valor: 0, compensar: true, ...props.selectedItem }, mode: 'onBlur' });

  useEffect(
    () => {
      if (!formProps.getValues().nroOrdem) {
        if (instanceOfGuiaGps(props.parentFormProps.getValues())) {
          const { compensacoes } = props.parentFormProps.getValues();
          if (compensacoes && compensacoes.length > 0) {
            formProps.setValue('nroOrdem', +compensacoes.reduce((prev, current) => (prev.nroOrdem > current.nroOrdem ? prev : current)).nroOrdem + 1);
          } else {
            formProps.setValue('nroOrdem', 1);
          }
        }
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const instanceOfGuiaGps = (object: any): object is GuiaGps => {
    return 'compensacoes' in object;
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <InputGroupInline id="nroOrdem" formProps={formProps} hidden={true} />
      <CompensacaoIS id="compensacao" formProps={{ ...formProps, validation: { required: true } }} />
      <Row>
        <Col>
          <InputGroupInline formProps={formProps} id="valor" isPrependLabel={true} label="Valor" type={InputType.NUMBER} />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default CompensacaoGpsEditView;
