import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/compensacaoExample';

const CompensacaoIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'referencia',
            text: 'Referência',
          },
          {
            dataField: 'valor',
            text: 'Valor',
          },
          {
            dataField: 'retencaoFonte',
            text: 'Retenção na Fonte',
          },
          {
            dataField: 'observacao',
            text: 'Observação',
          },
          {
            dataField: 'tomador.nome',
            text: 'Tomador',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'CompensacaoSearchView',
      }}
      label={props.label ? props.label : 'Compensação'}
      placeholder={props.placeholder ? props.placeholder : 'Informe a Compensação'}
    />
  );
};

export default CompensacaoIS;
